import clsx from 'clsx';

import images from '../../../assets/img';
import Button from '../../Button';
import styles from './ExerciseWelcomeScreen.module.scss'

function ExerciseWelcomeScreen({ action }) {
    const startExercise = () => {
        action(2)
    }

    return (
        <div className={clsx(styles.wrapper)}>
            <div className={clsx(styles.image)}>
                <img src={images.exerciseIntro} alt="do you speak english?" />
            </div>
            <h1 className={clsx(styles.title)}>Khoan </h1>
            <p className={clsx(styles.desc)}>Hy th ni vi cu  kim tra trnh  ca bn trc nh?
            </p>
            <div className={clsx(styles.btnWrapper)}>
                <p className={clsx(styles.cta)}>Bn  sn sng cha?</p>
                <Button onClick={startExercise} className={clsx(styles.btn)}>Bt u</Button>
            </div >
        </div>
    );
}

export default ExerciseWelcomeScreen;