import clsx from 'clsx'
import Countdown from 'react-countdown'

import { default as CountdownElement } from '../Countdown'
import Support from '../Support'
import PackageItem from './PackageItem'
import styles from './PriceList.module.scss'


const packages = [
    {
        name: 'Gi c bit',
        desc: [
            { descItem: '2 nm hc', value: true },
            { descItem: 'Thm 6 thng h', value: true },
            { descItem: 'Kho 10 ngy pht m', value: true },
            { descItem: 'Kha ly li cn bn', value: true },
        ],
        bestseller: true,
        price: {
            oldPrice: '6,120,000 VN',
            newPrice: '1,290,000 VN',
        }
    },
    {
        name: 'Gi 2 nm',
        desc: [
            { descItem: '2 nm hc', value: true },
            { descItem: 'Thm 6 thng h', value: false },
            { descItem: 'Kho 10 ngy pht m', value: false },
            { descItem: 'Kha ly li cn bn', value: false },
        ],
        bestseller: false,
        price: {
            oldPrice: '6,120,000 VN',
            newPrice: '1,290,000 VN',
        }
    },
    {
        name: 'Gi 1 nm tng 3 thng',
        desc: [
            { descItem: '2 nm hc', value: true },
            { descItem: 'Thm 3 thng h', value: true },
            { descItem: 'Kho 10 ngy pht m', value: false },
            { descItem: 'Kha ly li cn bn', value: false },
        ],
        bestseller: false,
        price: {
            oldPrice: '1,290,000 VN',
            newPrice: '890,000 VN',
        }
    },
]

function PriceList() {
    return (
        <div className={clsx(styles.listWrapper)}>
            <h2 className="fs-sec-heading fw-bold text-center">Mc gi u i dnh ring cho bn
                <br />Ch 1 ln duy nht
            </h2>
            <Countdown renderer={CountdownElement} date='2022-11-07T00:00:00' />
            <p className={clsx(styles.textCta)}>Chn gi thanh ton</p>
            <div className={clsx(styles.packageWrapper)}>
                {packages.map((packageItem, index) => (
                    <PackageItem key={index} packageInfo={packageItem} />
                ))}
            </div>
            <Support isLogin />
        </div>
    )
}

export default PriceList